import React from "react";
import { MDXProvider } from "@mdx-js/react";

const components = {
  h1: props => <h1 className="text-4xl text-violet-900 font-bold" {...props} />,
  h2: props => <h2 className="text-3xl text-violet-900 font-semibold" {...props} />,
  h3: props => <h3 className="text-2xl text-violet-900 font-semibold" {...props} />,
  blockquote: props => <blockquote className="text-sm border-violet-500 border-l-2 pl-4 pt-1 pb-1" {...props} />,
  a: props => <a className="border-b-2 border-violet-300 hover:border-violet-700 text-violet-500" {...props} />,
  hr: props => <hr className="border-violet-400" {...props} />,
  ul: props => <ul className="list-disc list-inside" {...props} />,
  ol: props => <ol className="list-decimal list-inside" {...props} />
}

const PostMDXProvider = ({ children }) => {
    return <MDXProvider components={components}>{children}</MDXProvider>
}

export default PostMDXProvider